import { ComponentsModule } from './../components/components.module';
import { GlobalModule } from './../global/global.module';
import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule, IonicPageModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { RestangularModule } from 'ngx-restangular';
import { MyApp } from './app.component';
import { CalendarModule } from "ion2-calendar";
@NgModule({
    declarations: [
        MyApp,
    ],
    imports: [
        BrowserModule,
        GlobalModule,
        CalendarModule,
        IonicPageModule,
        ComponentsModule,
        IonicModule.forRoot(MyApp, {
            backButtonText: '',
            iconMode: 'ios',
            modalEnter: 'modal-slide-in',
            modalLeave: 'modal-slide-out',
            tabsPlacement: 'bottom',
            pageTransition: 'ios-transition'
        }, 
{
  links: [
    { loadChildren: '../pages/traveler/traveler.module#TravelerPageModule', name: 'traveler', segment: 'traveler', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/airplane/airplane.module#AirplanePageModule', name: 'airplane', segment: 'airplane', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module#HomePageModule', name: 'home', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/hotel/hotel.module#HotelPageModule', name: 'HotelPage', segment: 'hotel', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/inquire/inquire.module#InquirePageModule', name: 'inquire', segment: 'inquire', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/international/international.module#InternationalPageModule', name: 'InternationalPage', segment: 'international', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/train/train.module#TrainPageModule', name: 'TrainPage', segment: 'train', priority: 'low', defaultHistory: [] }
  ]
}),
        // InquirePageModule.forRoot(),
        RestangularModule.forRoot((RestangularProvider) => {
            RestangularProvider.setBaseUrl('api');
            // RestangularProvider.setDefaultHeaders({'AuthoriAuzation': 'Bearer UDXPx-Xko0w4BRKajozCVy20X11MRZs1'});
        })
    ],
    bootstrap: [IonicApp],
    entryComponents: [
        MyApp,
    ],
    providers: [
        StatusBar,
        SplashScreen,
        { provide: ErrorHandler, useClass: IonicErrorHandler }
    ]
})
export class AppModule { }



